.block-editor-block-list__block[data-type="carkeek-blocks/slider"] .wp-block {
  display: none; }
  .block-editor-block-list__block[data-type="carkeek-blocks/slider"] .wp-block:first-of-type {
    display: block; }

.block-editor-block-list__block[data-type="carkeek-blocks/slider"].is-selected .wp-block, .block-editor-block-list__block[data-type="carkeek-blocks/slider"].has-child-selected .wp-block {
  display: block; }
